export default {
	props: ['leftColor', 'rightColor'],
	data() {
		return {
			ctx: null
		};
	},
	mounted() {
		this.drawLinear();
	},
	methods: {
		drawLinear() {
			this.ctx = uni.createCanvasContext('linear-gradient', this);
			let fallColor = this.ctx.createLinearGradient(0,0,this.fall_width,0);
			fallColor.addColorStop(0, '#69C311');
			fallColor.addColorStop(1, '#009900');
			this.ctx.fillRect(0,0,100, 100);
			this.ctx.draw();
			
		}
	}
}
